<template>
  <div id="login">
    <div id="login_head">
      <img src="../../static/img/logoaaaa.png" alt="" />
      <p>         </p>
    </div>
    <div class="login_center">
      <div class="login_text">
        <i class="iconfont icon-tubiao211 ilogin1"></i>
        <input type="text" name="" value="" placeholder="      "/>
      </div>
    </div>
    <div class="login_center">
      <div class="login_text">
        <i class="iconfont icon-mima1 ilogin1"></i>
        <input type="password" name="" value="" placeholder="    "/>
      </div>
    </div>
    <div id="login_code">
      <div id="login_import">
        <i class="iconfont icon-yanzhengma ilogin1"></i>
        <input type="text" name="" value="" placeholder="    " style="width:1.3rem;height:0.4rem;border:none;text-indent: 0.15rem"/>
      </div>
      <input type="button" id="login_click" value="" ref="mybox" @click="time()" />
    </div>
    <div id="login_password">
      <div class="login_rember">
        <input type="checkbox" name="" id="" value="" />
        <span></span>
      </div>
      <a href="#">    ?</a>
    </div>
    <router-link to="/personal" class="login_submit">
      <p>  </p>
    </router-link>
    <div class="login_submit">
      <p>    </p>
    </div>
    <div class="remind">
      <p></p>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
    export default {
      data(){
        return{
          wait:60
        }
      },
      methods:{
        time() {
          var that=this;
          if (this.wait == 0) {
            this.$refs.mybox.removeAttribute("disabled");
            this.$refs.mybox.value="";
            this.wait = 60;
          } else {
            this.$refs.mybox.setAttribute("disabled", true);
            this.$refs.mybox.value=this.wait+"";
            this.wait--;
            setTimeout(function() {
                that.time()
              },
              1000)
          }
        },
      }

    }
</script>

<style scoped>

</style>



